body {
	font: 'Raleway', sans-serif;
    color: rgba(0,0,0,.5);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: .5em;
    font-size: 1.5em;
    font-weight: bold;
    /* top: 15%; */
    margin: 10% auto;
	  background: linear-gradient(to right, red , yellow); /* Standard syntax */
}

button {
  background-color: rgba(25, 181, 254, 0.7);
  border: none;
  color: white;
  padding: 70px;
  text-align: center;
  display: inline-block;
  margin: 40px auto;
  border-radius: 50%;
  /* border: 5px solid rgba(25, 181, 254, 0.05); */
  cursor: pointer;
  box-shadow: 0px 0px 20px #ccc;
}

button:active {
  background: #e5e5e5;
  box-shadow: 0px 0px 20px #ccc;
  -webkit-box-shadow: inset 0px 0px 5px #c1c1c1;
     -moz-box-shadow: inset 0px 0px 5px #c1c1c1;
          box-shadow: inset 0px 0px 5px #c1c1c1;
   outline: none;
}

button:focus {
  outline: none;
}
